body::after {
    content: "";
    background-color: #0b1e46cc;
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    opacity: 0;
    transition: opacity 0.8s ease 0s;
    pointer-events: none;
    z-index: 149;
}
.popup-show body::after {
    opacity: 1;
}
.popup {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    padding: 30px 10px;
    transition: visibility 0.8s ease 0s;
    visibility: hidden;
    pointer-events: none;
}
.popup_show {
    z-index: 150;
    visibility: visible;
    overflow: auto;
    pointer-events: auto;
}
.popup_show .popup__content {
    visibility: visible;
    transform: scale(1);
}
.popup__wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    min-height: 100%;
    flex: 1 1 auto;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.popup__content {
    visibility: hidden;
    transform: scale(0);
    transition: transform 0.3s ease 0s;
    background-color: #f6faff;
    padding: 40px;
    width: 100%;
    max-width: 454px;
    border-radius: 8px;
    .lock & {
        visibility: visible;
    }
    @media (max-width: $mobile) {
        padding: 30px 15px;
    }
}

.popup__close {
    position: absolute;
    right: -41px;
    top: 10px;
    display: block;
    background: transparent;
    svg {
        fill: #fff;
    }
    @media (max-width: $mobile) {
        right: 0;
        top: -28px;
    }
}

.popup {
    // .popup__body

    &__body {
    }

    // .popup__top

    &__top {
        margin-bottom: 2rem;
        @media (max-width: $mobile) {
            margin-bottom: 1.5rem;
        }
    }

    // .popup__top-title

    &__top-title {
        font-size: 18px;
        margin: 0px 0px 10px 0px;
        @media (max-width: $mobileSmall) {
            font-size: 16px;
            margin: 0px 0px 5px 0px;
        }
    }

    // .popup__title

    &__title {
        font-size: 18px;
        margin-bottom: 1.5rem;
        @media (max-width: $mobile) {
            font-size: 16px;
            margin-bottom: 1rem;
        }
    }

    // .popup__price

    &__price {
        font-size: 20px;
        @media (max-width: $mobile) {
            font-size: 18px;
        }
    }

    // .popup__center

    &__center {
    }

    // .popup__inputs

    &__inputs {
        position: relative;
        background-color: #ecf1f7;
        border-radius: 0.75rem;
        padding: 1rem;
        display: flex;
        align-items: center;
        height: 56px;
        transition: color 0.3s ease 0s;
        border: 1px solid transparent;
        &._form-focus {
            box-shadow: rgba(46, 113, 252, 0.77) 0px 0px 0px 0.767286px inset;
            background-color: rgba(236, 241, 247, 0.23);
            label {
                position: relative;
                top: 0;
                transform: translate(0px, 0px);
                color: $mainColor;
                font-size: 0.8156rem;
            }
            input {
                &::placeholder {
                    opacity: 1;
                }
            }
        }
        &._form-error {
            background-color: #f23c3c1f;
            box-shadow: none;
            label {
                color: #e33333;
                top: 0;
                font-size: 0.8156rem;
            }
        }
        @media (max-width: $mobile) {
            padding: 5px 10px;
        }
    }

    // .popup__input

    &__input {
        display: flex;
        flex-direction: column;
        width: 100%;
        label {
            line-height: 1.5;
            position: absolute;
            top: 50%;
            transform: translate(0px, -50%);
            color: #222d4185;
            transition:
                color 0.3s ease 0s,
                top 0.3s ease;
            @media (max-width: $mobile) {
                font-size: 12px;
            }
        }
        &.input_expire {
            opacity: 0;
            transform: translate3d(100%, 0, 0);
            transition-property: transform;
            transition-duration: 150ms, 0.3s;
            transition-timing-function: ease-in-out;
            position: absolute;
            pointer-events: none;
            .active & {
                opacity: 1;
                pointer-events: auto;
                transform: translate3d(6.5625rem, 0, 0);
            }
        }
        &.input_cvv {
            opacity: 0;
            transform: translate3d(100%, 0, 0);
            transition-property: transform;
            transition-duration: 150ms, 0.3s;
            transition-timing-function: ease-in-out;
            position: absolute;
            pointer-events: none;
            .active & {
                opacity: 1;
                pointer-events: auto;
                margin-left: 7.1875rem;
                transform: translate3d(6.5625rem, 0, 0);
                @media (max-width: $mobileSmall) {
                    margin-left: 60px;
                }
            }
        }
        &.popup-card {
            input {
                transition-property: transform;
                transition-duration: 150ms, 0.3s;
                transition-timing-function: ease-in-out;
                transform: translate3d(0, 0, 0);
                .active & {
                    opacity: 0;
                    pointer-events: none;
                    transform: translate3d(-50%, 0, 0);
                }
            }
        }
        input {
            background: transparent;
            position: relative;
            z-index: 5;
            font-size: 16px;
            &::placeholder {
                opacity: 0;
                color: #222d4185;
            }
            @media (max-width: $mobile) {
                font-size: 12px;
            }
        }
        .t-value {
            position: absolute;
            top: 1.7rem;
            width: 100px;
            @media (max-width: $mobile) {
                top: 1.9rem;
                font-size: 12px;
            }
        }
    }

    // .popup__icons

    &__icons {
        position: absolute;
        right: 0.75rem;
        top: 50%;
        transform: translate(0, -50%);
        display: flex;
        align-items: center;
        img {
            height: 1.5rem;
            margin-right: 0.25rem;
            @media (max-width: $mobileSmall) {
                margin: 0px 0px 0px 0px;
                height: 1rem;
            }
        }
        @media (max-width: $mobileSmall) {
            right: 5px;
        }
        .payment-mastercard {
            display: none;
        }
        .payment-visa {
            display: none;
        }
        .payment-maestro {
            display: none;
        }
        .payment-close {
            display: none;
            width: 24px;
            height: 24px;
            cursor: pointer;
            opacity: 0.56;
            justify-content: center;
            align-items: center;
            svg {
                fill: rgba(34, 45, 65, 0.596);
            }
            @media (max-width: $mobileSmall) {
                width: 20px;
                height: 20px;
            }
        }
    }

    // .popup__button

    &__button {
        margin-bottom: 1rem;
        font-weight: 400;
        width: 100%;
        letter-spacing: 0.2px;
        //pointer-events: none;
        opacity: 0.56;
        border-radius: 0.5rem;
        margin-top: 1.5rem;
        background-color: #2e71fc;
        color: #ffffff;
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
        height: 56px;
        padding: 10px;
        &.active {
            pointer-events: auto;
            opacity: 1;
        }
        @media (max-width: $mobile) {
            height: 50px;
            margin: 16px 0px 0px 0px;
        }
    }
    // .popup__alert-error

    &__alert-error {
        color: #f23c3c;
        padding: 20px;
        margin-top: 1.5rem;
        font-size: 14px;
        width: 100%;
        border-radius: 8px;
        text-align: center;
        background: linear-gradient(#f23c3c1f, #f23c3c1f), #f6faff;
        line-height: 150%;
    }

    // .popup__bottom

    &__bottom {
    }

    // .popup__footer

    &__footer {
    }
    // .popup__bottom

    &__bottom {
    }
    // .popup__footer

    &__footer {
    }
    // .popup__subtitle

    &__subtitle {
        margin-top: 0.5rem;
        text-align: center;
        font-size: 14px;
    }
    // .popup__status-icon

    &__status-icon {
        margin-bottom: 1rem;
        display: flex;
        justify-content: center;
        align-items: center;
        img {
            width: 180px;
            height: 180px;
        }
    }
}

.form-error {
    line-height: 1.5rem;
    color: #e33333;
    font-size: 0.8156rem;
}

.bottom-popup {
    padding-top: 1rem;
    display: flex;
    flex-direction: column;
    gap: 18px 0px;
    .popup__inputs {
        height: 44px;
        padding: 3px 10px;
        display: flex;
        &._form-focus {
            label {
                font-size: 12px;
                color: #222d4185;
            }
        }
        &._form-error {
            label {
                font-size: 12px;
                color: #e33333;
            }
        }
        input {
            font-size: 12px;
        }
        &.hidden {
            display: none;
        }
    }
    // .bottom-popup__title

    &__title {
        display: flex;
        font-size: 14px;
        justify-content: space-between;

        &.save-title {
            justify-content: start;
            align-items: center;
        }
    }

    // .bottom-popup__icon

    &__icon {
        position: relative;
        display: flex;
        padding: 2px;
        margin: 0px 0px 0px 5px;
        cursor: pointer;
        svg {
            fill: #222d4152;
        }
        &:hover {
            .bottom-popup__tippy {
                opacity: 1;
                pointer-events: auto;
            }
        }
    }

    // .bottom-popup__tippy

    &__tippy {
        position: absolute;
        opacity: 0;
        pointer-events: none;
        background-color: #575b61;
        color: #fff;
        bottom: 150%;
        left: 50%;
        transform: translate(-50%, 0);
        z-index: 10;
        width: 288px;
        line-height: 1.25rem;
        padding: 0.75rem 1rem;
        border-radius: 0.75rem;
        transition: opacity 0.3s ease 0s;
        &::before {
            content: "";
            position: absolute;
            bottom: -8px;
            left: 50%;
            inline-size: 0.5rem;
            block-size: 0.5rem;
            border-radius: 0.125rem;
            box-sizing: border-box;
            background: inherit;
            transform: translate(-50%, -50%) rotate(45deg);
            @media (max-width: $mobileSmall) {
                left: 90%;
            }
        }
        @media (max-width: $mobileSmall) {
            left: 200%;
            transform: translate(-100%, 0);
            font-size: 14px;
            padding: 5px 10px;
        }
    }
}

.switch {
    position: relative;
    display: inline-block;
    width: 48px;
    height: 24px;
    overflow: hidden;
    cursor: pointer;

    &__checkbox {
        display: none;
        &:checked + .switch__slider {
            background-color: #2e71fc;
        }
        &:checked + .switch__slider::after {
            opacity: 0;
        }
        &:checked + .switch__slider::before {
            transform: translateX(24px);
        }
        &:checked + .switch__slider .switch__check {
            left: 0;
        }
    }

    // .switch__check
    &__check {
        pointer-events: none;
        display: block;
        width: 24px;
        height: 24px;
        position: absolute;
        top: 50%;
        left: -25px;
        transform: translate(0px, -50%);
        z-index: 10;
        transition: left 0.3s ease;
        svg {
            fill: #fff;
        }
    }

    &__slider {
        position: absolute;
        cursor: pointer;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: #e2e8ef;
        border-radius: 2rem;
        transition: 0.3s;
        &:hover {
            background-color: #a2a8ae;
        }
        &::before {
            content: "";
            position: absolute;
            height: 16px;
            width: 16px;
            left: 5px;
            top: 4px;
            background-color: white;
            -webkit-transition: 0.4s;
            transition: 0.4s;
            border-radius: 50%;
        }
        &::after {
            content: "";
            transition-property: transform;
            transition-duration: 150ms, 0.3s;
            transition-timing-function: ease-in-out;
            position: absolute;
            top: 50%;
            transform: translate(0px, -50%);
            right: 5px;
            border-radius: 50%;
            width: 12px;
            height: 12px;
            border: 2px solid #a2a8ae;
        }
    }
}

.footer-popup {
    margin-top: 3rem;
    @media (max-width: $mobile) {
        margin-top: 1.5rem;
    }
    // .footer-popup__images

    &__images {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        align-items: center;
        gap: 16px;
        svg {
            filter: brightness(0) saturate(100%) invert(93%) sepia(6%) saturate(513%) hue-rotate(177deg) brightness(88%)
                contrast(88%);
        }
    }

    // .footer-popup__copy

    &__copy {
        margin-top: 1.5rem;
        display: flex;
        justify-content: center;
        align-items: center;
        svg {
            fill: rgb(192, 201, 212);
        }
    }
}

#success {
    .popup__button {
        pointer-events: auto;
        opacity: 1;
        margin-bottom: 0px;
    }
    .popup__title {
        font-size: 20px;
        text-align: center;
        font-weight: 500;
        margin: 0px 0px 0px 0px;
    }
    .popup__price {
        margin-top: 1rem;
        text-align: center;
        font-size: 28px;
        @media (max-width: $mobile) {
            font-size: 20px;
        }
    }
}
